@import "../../_module";
.letter {
  width: 100%;
  @include device__query(tablet) {
    @include position;
  }
  &__wrap {
    width: 100%;
    padding-top: 35px;
    padding-bottom: 70px;
    text-align: center;
    > div {
      &:nth-child(3) {
        margin-top: 30px;
        @include device__query(tablet) {
          margin-top: 0;
        }
      }
    }
  }
  &__content {
    @include flex__column;
    row-gap: 32px;
    .greet {
      @include flex__column;
      justify-content: flex-start;
      align-items: center;
      row-gap: 12px;
      text-align: center;
      p {
        white-space: pre-line;
        font-size: $font-size--ssm;
        line-height: 1.6em;
        color: $text-color--temp;
      }
    }
    .detail {
      @include flex__column;
      justify-content: flex-start;
      align-items: center;
      row-gap: 8px;
      margin-bottom: 8px;
      @include device__query(tablet) {
        @include position;
      }
      * {
        line-height: 1.5em;
      }
      p {
        @include flex__row;
        justify-content: center;
        align-items: center;
        font-size: $font-size--default;
        font-weight: $font-weight--Medium;
        color: $text-color--temp;
        letter-spacing: 0.05em;
        img {
          width: 16px;
          height: 16px;
          margin-bottom: 4px;
          margin-right: 5px;
          &:last-of-type {
            margin-left: 8px;
          }
        }
      }
      span {
        display: inline-block;
        margin-left: 8px;
        margin-right: 16px;
        font-size: $font-size--sm;
        font-weight: $font-weight--Regular;
        color: $text-color--disable;
      }
    }
  }
}
